<template>
  <div class="header">
    <header class="header-list">
      <nav v-for="(item,index) in headerList"
           :key="index">
        <a style="cursor: pointer;font-weight: 600;"
           @click="goTo(item)"> {{item}}</a>
      </nav>
    </header>
  </div>
</template>
<script>
export default {
  data () {
    return {
      headerList: [
        'Home', 'Icons', 'Docs', 'Support'
      ]
    }
  },
  methods: {
    goTo (item) {
      var routerNames = item.toLocaleLowerCase()
      if (this.$route.path.search(routerNames) !== -1) {
        console.log('包含')
        return
      }
      this.$router.push({ // 核心语句
        path: '/' + (routerNames === 'docs' ? 'docs/BasicUse' : routerNames), // 跳转的路径
        query: { // 路由传参时push和query搭配使用 ，作用时传递参数
          id: this.id
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
@import "~@/theme/index.less";
.header {
  height: 1.5rem;
  background-color: #155592;
  display: flex;
  align-items: center;
  width: 100%;
  .header-list {
    width: 100%;
    display: flex;
    justify-content: space-around;
    color: @index-font-color;
    font-size: 16px;
    font-weight: 600;
  }
}
@media only screen and (max-width: 767px) {
  .header {
    background-color: #74b816;
    .header-list {
    }
  }
}
</style>
